/**
* Based on copypasta from Remy Bach and Sarah Drasner
*/
.BlogPostLayout_content__2665A code,
.BlogPostLayout_content__2665A pre {
  color: white;
  font-family: Consolas,Menlo,Monaco,source-code-pro,Courier New,monospace;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
       tab-size: 4;
  -webkit-hyphens: none;
      -ms-hyphens: none;
          hyphens: none;
}
  
/* Code blocks */
.BlogPostLayout_content__2665A pre {
  overflow: auto;
  padding: 1em;
}

.BlogPostLayout_content__2665A :not(pre) > code,
.BlogPostLayout_content__2665A pre {
  background: hsla(0,0%,0%,0.9);
}
  
.BlogPostLayout_content__2665A pre::-moz-selection,
.BlogPostLayout_content__2665A pre::-moz-selection {
  background: hsl(207, 4%, 16%);
}
  
.BlogPostLayout_content__2665A pre::-moz-selection,
.BlogPostLayout_content__2665A pre::selection {
  background: hsl(207, 4%, 16%);
}
  
/* Text Selection colour */
.BlogPostLayout_content__2665A pre::-moz-selection,
.BlogPostLayout_content__2665A pre ::-moz-selection {
  text-shadow: none;
  background: hsla(0, 0%, 100%, 0.15);
}

.BlogPostLayout_content__2665A pre::selection,
.BlogPostLayout_content__2665A pre ::selection {
  text-shadow: none;
  background: hsla(0, 0%, 100%, 0.15);
}

/* Inline code */
.BlogPostLayout_content__2665A :not(pre) > code {
  border-radius: .3em;
  background: rgba(255,229,100,0.2);
  color: #1a1a1a;
  padding: .15em .2em .05em;
  white-space: normal;
}
.BlogPostLayout_content__2665A a > code {
